import {mapState} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'

export default {
	components: {
		uniNumberBox,uniSwipeAction
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			goodsList:[],
			
			options1: [{
				text: '删除',
				style: {
					backgroundColor: 'rgb(255,58,49)'
				}
			}],
		};
	},
	onShow(){
		this.loadData();
	},
	watch:{
		//显示空白页
		cartList(e){
			let empty = e.length === 0 ? true: false;
			if(this.empty !== empty){
				this.empty = empty;
			}
		}
	},
	computed:{
		...mapState(['hasLogin'])
	},
	methods: {
		stopPrevent(){},
		//请求数据
		async loadData(){
			//加载购物车数据
			let cars = await this.$request.ModelOrder.getShopCar();
			let list = [];
			if(cars && cars.records){
				cars.records.forEach(item => {
					list.push({
						id: item.id,
						productId: item.productId,
						productSpecId: item.productSpecId,
						image: item.picUrlList && item.picUrlList.length>0 && item.picUrlList[0],
						attr_val: item.specNames,
						stock: 20,
						title: item.name,
						price: item.price,
						number: item.num
					})
				})
			}
			let cartList = list.map(item=>{
				item.checked = true;
				return item;
			});
			this.cartList = cartList;
			this.calcTotal();  //计算总价
			
			/* 猜你喜欢 */
			let goodsList = await this.$request.ModelHome.getGoodsRecommend();
			this.goodsList = goodsList.records || [];
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		//详情页
		navToDetailPage(id) {
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		check(type, index){
			if(type === 'item'){
				this.cartList[index].checked = !this.cartList[index].checked;
			}else{
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item=>{
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data){
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		//删除
		async deleteCartItem(index){
			let list = this.cartList;
			let row = list[index];
			let id = row.id;

			let ids = [];
			ids.push(id);
			let result = await this.$request.ModelOrder.delShopCar(JSON.stringify(ids));
			if(result.code == 'ok'){
				this.cartList.splice(index, 1);
				this.calcTotal();
				uni.hideLoading();
			}else{
				this.$api.msg('删除异常，清稍后重试');
			}
		},
		async deleteAll(){
			var that = this;
			let list = that.cartList;
			let ids = [];
			list.forEach(item=>{
				if(item.checked){
					ids.push(item.id);
				}
			})
			if(ids.length == 0){
				return;
			}
			uni.showModal({
			    title: '提示',
				content: '确定删除吗?',
			    success: function (res) {
			        if (res.confirm) {
			            that.$request.ModelOrder.delShopCar(JSON.stringify(ids)).then(result => {
							if(result.code == 'ok'){
								that.loadData();
							}else{
								that.$api.msg('删除异常，清稍后重试');
							}
						});
			        }
			    }
			});
		},
		//清空
		clearCart(){
			uni.showModal({
				content: '清空购物车？',
				success: (e)=>{
					if(e.confirm){
						this.cartList = [];
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			let list = this.cartList;
			if(list.length === 0){
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item=>{
				if(item.checked === true){
					total += item.price * item.number;
				}else if(checked === true){
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder(){
			let list = this.cartList;
			let orderProducts = [];
			list.forEach(item=>{
				if(item.checked){
					orderProducts.push({
						productId: item.productId,
						productSpecId: item.productSpecId,
						quantity: item.number
					})
				}
			})
			uni.redirectTo({
				url: '/pages/order/createOrder?order=' + JSON.stringify(orderProducts)
			})
		},
		navTo(url){
			uni.navigateTo({  
				url
			})  
		}
	}
}
